body {
  margin: 0;
  padding: 0;
  font-family: "Ubuntu", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.app {
  text-align: center;
}
.app-header {
  background-color: darkblue;
  left: 0;
  padding-top: 20px;
  position: relative;
  right: 0;
  top: 0;
  z-index: 5000;
}
h1 {
  color: white;
  font-size: 26px;
}
ul {
  list-style: none;
}
.app-header ul li {
  color: white;
  display: inline-block;
  margin: 20px;
}
a {
  text-decoration: none;
}
a:visited {
  color: darkgray;
}
a:hover {
  color: lightblue;
  font-weight: 500;
}
a:focus {
  outline: 0;
}
.recipes-container {
    height: 100vh;
    margin: -20px auto 0;
    max-width: 1200px;
    padding-top: 20px;
}
.recipe-modal-bg {
    background-color: rgba(0, 0, 0, 0.5);
    bottom: 0;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 100;
}
.background-fixed {
    clip: rect(auto auto auto auto);
    height: 100%;
    left: 50%;
    max-width: 1200px;
    overflow: hidden;
    position: absolute;
    top: 100vh;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    width: 100vw;
    z-index: 1;
}
.recipes-list {
    background-color: rgba(255, 255, 255, 0.5);
    border: 1px solid #333;
    margin: 20px;
    -webkit-margin-after: 0;
            margin-block-end: 0;
    -webkit-margin-before: 0;
            margin-block-start: 0;
    -webkit-padding-start: 0;
            padding-inline-start: 0;
    position: relative;
    z-index: 200;
}
.name {
    font-size: 18px;
    max-width: 350px;
    padding-left: 20px;
    width: 45%;
}
.time {
    max-width: 150px;
    width: 20%;
}
.main-ingredient {
    max-width: 250px;
    width: 35%;
}
.recipe-container {
    background-color: beige;
    border-radius: 20px;
    left: 12.5%;
    margin: 5vh auto;
    max-height: 90vh;
    max-width: 500px;
    padding: 5px 15px 15px;
    position: absolute;
    right: 12.5%;
    top: 5vh;
    width: 75%;
    z-index: 750;
}
.recipes-container * {
    box-sizing: border-box;
}
.recipe {
    display: flex;
    justify-content: space-between;
    list-style: none;
    padding: 5px 0;
    text-align: left;
}
.recipe:nth-child(2n){
    background-color: beige;
}
.recipe:hover {
    background-color: blanchedalmond;
}
.close-x {
    background-color: #fff;
    border: 1px solid #333;
    border-radius: 50%;
    padding: 0 5px;
    position: absolute;
    right: 20px;
    text-align: left;
    top: 10px;
    width: 20px;
}
.close-x:hover {
    background-color: blanchedalmond;
}

.ingredients-list {
    list-style: none;
    max-height: 35vh;
    overflow-y: auto;
    padding: 10px 40px;
    text-align: left;
}
ul li {
    display: block;
    margin: 0;
}
.instructions-list {
    list-style: none;
    max-height: 35vh;
    overflow-y: auto;
    padding: 10px 40px;
    text-align: left;
}
ul li {
    display: block;
    margin: 0;
}
.main-recipe-form {
    margin: 0 auto;
    max-width: 750px;
}
label {
    display: block;
    margin: 15px 0 0 15px;
    text-align: left;
}
input {
    border: 1px solid #ccc;
    box-shadow: 2px 2px 2px 0 #ccc;
    display: block;
    font-size: 16px;
    height: 30px;
    margin: 0 15px;
    text-align: left;
}
.ingredient {
    border: none;
    box-shadow: none;
}
.ingredients input {
    display: inline-block;
}
.qty,
.unit {
    width: 50px;
}
.instruction {
    border: none;
    box-shadow: none;
}

.instructions input {
    display: inline-block;
    width: 70%;
}
